import React, { useState, useEffect } from 'react';
import { hot } from 'react-hot-loader';
import { Link } from 'react-router-dom';
import { useModal } from 'react-simple-modal-provider';

import { useMediaPredicate } from 'react-media-hook';
import { mobileBreakpoint } from '../helpers/Media';

import VideoButtonPlayIcon from '../components/SvgRobo/VideoButtonPlayIcon';
import VideoRoundIcon from '../components/SvgRobo/VideoRoundIcon';

import CoolLightBox from '../components/CoolLightBox';
import CountDownTimer from '../components/CountDownTimer';
import Card from '../components/Card';

import roborovskiSignImg from '../assets/images/roborovski-sign.png';
import roborovskiImg from '../assets/images/roborovski.png';
import roborovskiX2Img from '../assets/images/roborovski@2x.png';

import badgeAwardImg from '../assets/images/badge/badge-award.svg';
import badgeSignImg from '../assets/images/badge/badge-sign.png';
import badgeSignX2Img from '../assets/images/badge/badge-sign@2x.png';

import mintEmoji1Img from '../assets/images/mint/emoji-1.png';
import mintEmoji2Img from '../assets/images/mint/emoji-2.png';
import mintEmoji3Img from '../assets/images/mint/emoji-3.png';

import nft0Img from '../assets/images/nft/nft-0.png';
import nft0X2Img from '../assets/images/nft/nft-0@2x.png';
import nft2Img from '../assets/images/nft/nft-2.png';
import nft2X2Img from '../assets/images/nft/nft-2@2x.png';
import nft3Img from '../assets/images/nft/nft-3.png';
import nft3X2Img from '../assets/images/nft/nft-3@2x.png';

import {
  youtubeTeaserUrl,
  startOfSaleTimestamp,
} from '../configs';
import './ScreenFirst.scss';



function ScreenFirst(props) {
  const isMobile = useMediaPredicate(mobileBreakpoint);
  const { open: openMintModal } = useModal('MintModal');
  const [state, setState] = useState({ hover: false, hover2: false });

  const [isSoldOut, setSoldOut] = useState(false);
  const [isSaleOpen, setSaleOpen] = useState(false);
  useEffect(() => {
    const now = Math.trunc(new Date().getTime() / 1000);
    if (now > startOfSaleTimestamp) {
      setSaleOpen(true);
    }
    const intervalId = setInterval(() => {
      const now = Math.trunc(new Date().getTime() / 1000);
      if (now > startOfSaleTimestamp) {
        setSaleOpen(true);
      }
    }, 1000);
    return () => {
      clearInterval(intervalId);
    }
  }, []);

  return (
    <main className="main-screen" data-aos="fade-up">
      <div className="container">
        <div className="row">
          <div className="col-lg-6 col-xl-4">
            <div className="main" data-aos="fade-right">
              <div className="main__logo">
                <img
                  className={state.hover ? "animate__animated animate__bounce" : ""}
                  onMouseOver={() => setState({ hover: true })}
                  onMouseOut={() => setState({ hover: false })}
                  width="424" height="80" alt="" title=""
                  src={roborovskiSignImg}
                />
              </div>

              <p className="desc">Take a part of the origin story and the ultimate master plan of a little hamster named Roborovski. Discover this superb 10,000 piece collection of NFT artwork</p>

              <div className="main__countdown">
                <div className="main__countdown-item">
                  {!isSaleOpen && <CountDownTimer deadline={startOfSaleTimestamp} />}
                  {isSaleOpen && !isSoldOut && <div className="main__countdown-item-saleisopen">SALE IS OPEN</div>}
                  {isSaleOpen && isSoldOut && <div className="main__countdown-item-saleisopen">SOLD OUT</div>}
                </div>
                <div className="main__mint" onClick={openMintModal}>
                  <div className="main__mint-i">
                    <div className="main__mint-row">
                      <span>Mint</span> <img src={mintEmoji1Img} alt="" title="" />
                    </div>
                    <div className="main__mint-row">
                      <img src={mintEmoji2Img} alt="" title="" /><span>your</span>
                    </div>
                    <div className="main__mint-row">
                      <span>Robo</span><img src={mintEmoji3Img} alt="" title="" />
                    </div>
                  </div>
                </div>
              </div>

              <div className="main__roborovski">
                <img
                  className={state.hover2 ? "animate__animated animate__rubberBand" : ""}
                  onMouseOver={() => setState({ hover2: true })}
                  onMouseOut={() => setState({ hover2: false })}
                  width="240" height="240" alt="" title=""
                  src={roborovskiImg} srcSet={`${roborovskiX2Img} 2x`}
                />
              </div>
            </div>
          </div>

          <div className="col-lg-6 col-xl-3 offset-xl-5">
            <div className="main__lead" data-aos="fade-left">
              <div className="main__lead-actions">
                {isMobile && <CoolLightBox options={{ infinite: false }} >
                  <div className="video__box-btn"
                    data-fancybox=""
                    data-src={youtubeTeaserUrl}
                  >
                    <button className="btn-round"><VideoButtonPlayIcon /></button>
                  </div>
                </CoolLightBox>}

                {isMobile && <img alt="" title="" src={badgeAwardImg} />}

                <div className="howtobuy">
                  <Link to="/howtobuy"><span><i>How to Buy?</i></span></Link>
                </div>
              </div>

              <div className="fcards">
                <Card
                  poster={nft0Img}
                  posterhd={`${nft0X2Img} 2x`}
                  name="Roborovski #3832"
                  summ="1.76"
                  icon1="1"
                  icon2="4"
                  key="1"
                  className="fcard-1"
                />
                <Card
                  poster={nft2Img}
                  posterhd={`${nft2X2Img} 2x`}
                  name="Roborovski #24"
                  summ="16.28"
                  icon1="2"
                  icon2="5"
                  key="2"
                  className="fcard-2"
                />
                <Card
                  poster={nft3Img}
                  posterhd={`${nft3X2Img} 2x`}
                  name="Roborovski #24"
                  summ="16.28"
                  icon1="3"
                  icon2="6"
                  key="3"
                  className="fcard-3"
                />
              </div>
            </div>

            {!isMobile && <div className="main__video" data-aos="fade-down" data-delay="200">
              <CoolLightBox options={{ infinite: false }} >
                <div className="video__box"
                  data-fancybox=""
                  data-src={youtubeTeaserUrl}
                >
                  <div className="video__box-btn">
                    <button className="btn-round"><VideoButtonPlayIcon /></button>
                  </div>
                  <div className="video__box-item rotateAnimate">
                    <div className="video__box-color"></div>
                    <div className="video__box-round"><VideoRoundIcon /></div>
                    <div className="video__box-text">
                      <img
                        width="312" height="312" alt="" title="" className=""
                        src={badgeSignImg}
                        srcSet={`${badgeSignX2Img} 2x`}
                      />
                    </div>
                  </div>
                </div>
              </CoolLightBox>
            </div>}
          </div>
        </div>
      </div>
    </main>
  );
};

export default hot(module)(ScreenFirst);
