import { useState } from 'react';
import { hot } from 'react-hot-loader';
import { Visibility } from 'semantic-ui-react';
import Seo from './Seo';
import Header from './Header';
import Footer from './Footer';


function Page({ title, description, meta, children }) {
  const [state, setState] = useState({ fixed: false });
  const hideFixedMenu = () => setState({ fixed: false });
  const showFixedMenu = () => setState({ fixed: true });

  return (
    <>
      <Seo
        title={title}
        description={description}
        meta={meta ? meta : []}
      />
      <Visibility
        once={false}
        onBottomPassed={showFixedMenu}
        onBottomPassedReverse={hideFixedMenu}
      >
        <Header fixed={state.fixed} />
      </Visibility>
      {children}
      <Footer />
    </>
  );
};

export default hot(module)(Page);
